svg,
video {
  vertical-align: middle; }

figure {
  margin: 0; }

address,
blockquote,
table,
figure,
form,
fieldset,
legend,
pre,
dl,
ul,
ol,
hr,
p {
  margin-top: 0;
  margin-bottom: 1.5em; }

nav > ul,
nav > ol,
li > ul,
li > ol {
  margin-bottom: 0; }

ul, ol {
  margin-left: 0;
  padding-left: 2.5em; }

.text-uppercase {
  text-transform: uppercase; }

.mb15 {
  margin-bottom: 15px; }

.alternate-color {
  color: #41932b; }

body {
  font-weight: 600;
  overflow-x: hidden;
  background-color: #bdedfd; }

@media (min-width: 75em) {
  .container {
    max-width: 900px; } }

footer {
  padding: 20px;
  background: #003a65;
  color: #fff;
  font-size: 0.875rem;
  letter-spacing: 0.12em;
  text-transform: uppercase; }

.alternate-heading {
  font-family: "KGRed", sans-serif; }

.social-links {
  padding-left: 0;
  list-style: none; }
  .social-links li {
    display: inline-block;
    margin: 0 10px; }

.section {
  padding-top: 40px;
  padding-bottom: 40px; }

.section-content {
  position: relative;
  z-index: 10; }

.logo {
  background: url("../images/logo.png") no-repeat top left;
  width: 274px;
  height: 184px;
  display: inline-block;
  text-indent: 110%;
  white-space: nowrap;
  overflow: hidden;
  color: transparent;
  text-shadow: none;
  margin-bottom: 0; }

.loader {
  display: table;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background: #c5f3fb;
  opacity: 1;
  transition: opacity ease 1s; }
  .loader > .vcenter {
    display: table-cell;
    vertical-align: middle; }
  .loader.out {
    opacity: 0;
    z-index: -1; }
    .loader.out .spinner {
      opacity: 0; }

.spinner {
  margin: 0 auto;
  width: 200px;
  height: 200px;
  position: relative;
  transition: opacity ease .4s;
  background: url("../images/santa-run.gif") no-repeat; }

.btn-svg {
  position: relative;
  display: inline-block;
  width: 320px;
  height: 100px; }
  .btn-svg svg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%; }
  .btn-svg .stars-left,
  .btn-svg .stars-right {
    stroke: 1;
    stroke-color: #000;
    opacity: 0;
    visibility: hidden;
    transition: all ease .3s; }
  .btn-svg .shadow {
    fill: #c71828; }
  .btn-svg .surface {
    fill: #e51920;
    transition: all ease .3s; }
  .btn-svg .stars-left {
    transform: rotateZ(45deg); }
  .btn-svg .stars-right {
    transform-origin: 100% 100%;
    transform: rotateZ(-45deg); }
  .btn-svg span {
    font-weight: 900;
    color: #fff;
    text-shadow: 0 2px rgba(228, 93, 57, 0.5);
    font-size: 1.5625rem;
    letter-spacing: 0.05em;
    text-transform: uppercase;
    position: absolute;
    top: 26px;
    left: 0;
    width: 100%; }
  .btn-svg:hover {
    text-decoration: none; }
    .btn-svg:hover .surface {
      fill: #e82f36; }
    .btn-svg:hover .stars-left,
    .btn-svg:hover .stars-right {
      opacity: 1;
      visibility: visible;
      transform: rotateZ(0deg); }

button.btn-svg {
  background: none;
  border: none;
  outline: none; }

.btn-social {
  display: inline-block;
  width: 40px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  color: #fff;
  font-size: 1.25rem;
  border-radius: 50%;
  transition: all ease .3s; }
  .btn-social:hover, .btn-social:active {
    color: #fff;
    box-shadow: none;
    transform: translateY(4px); }
  .btn-social > i {
    text-shadow: 0 2px rgba(58, 88, 152, 0.5); }

.btn-facebook {
  background: #3a5898;
  box-shadow: 0 1px 0 #1e3361, 0 2px 0 #1e3361, 0 3px 0 #1e3361, 0 4px 0 #1e3361; }

.btn-twitter {
  background: #00aced;
  box-shadow: 0 1px 0 #008bbf, 0 2px 0 #008bbf, 0 3px 0 #008bbf, 0 4px 0 #008bbf; }

nav {
  max-width: 712px;
  position: relative;
  text-align: center;
  margin: 0 auto 30px;
  font-family: "KGRed", sans-serif;
  font-weight: 400;
  font-size: 0.875rem; }
  nav:before {
    content: '';
    width: 100%;
    height: 90px;
    position: absolute;
    top: -45px;
    left: 0;
    z-index: 9;
    background: rgba(255, 255, 255, 0.4); }
    @media (min-width: 48em) {
      nav:before {
        border-radius: 100%; } }
  nav.jelly:before {
    animation: grow-jelly 2000ms linear both; }
  nav a {
    position: relative;
    z-index: 10;
    color: #003a65;
    text-transform: uppercase;
    min-width: 200px;
    letter-spacing: 0.06em;
    display: inline-block;
    margin: 8px -10px; }
    nav a:before {
      content: '';
      background: url("../images/star.png") no-repeat;
      width: 18px;
      height: 18px;
      display: inline-block;
      transform: translateX(20px) rotateY(0deg);
      margin-right: 5px;
      vertical-align: text-bottom;
      opacity: 0;
      visibility: hidden;
      transition: opacity ease .7s, visibility ease 1s, transform ease-out .5s; }
    nav a:hover {
      color: #003a65;
      text-decoration: none; }
      nav a:hover:before {
        transition: opacity ease .3s, visibility ease .3s, transform ease-out .5s;
        visibility: visible;
        opacity: 1;
        transform: translateX(5px) rotateY(360deg); }

.grow-jelly-target {
  -webkit-animation: grow-jelly 1000ms linear both;
  animation: grow-jelly 1000ms linear both; }

