.loader {
  @include vcenter();
  
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
  width: 100%;
  height: 100%;
  background: #c5f3fb;
  opacity: 1;
  transition: opacity ease 1s;
  
  &.out {
    opacity: 0;
    z-index: -1;
    
    .spinner {
      opacity: 0;
    }
  }
}


.spinner {
  margin: 0 auto;
  width: 200px;
  height: 200px;
  position: relative;
  transition: opacity ease .4s;
  background: url('../images/santa-run.gif') no-repeat;
}

